.main {
    padding: 99px 0 0 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.container {
    margin: 0 auto;
    max-width: 1280px;
    width: 100%;
}

.cardContainer {
    display: grid;
    grid-template-columns: repeat(3, minmax(250px, 315px));
    grid-gap: 67px;
    padding: 0 78px 143px 78px;
    justify-content: center;
}

.breadcrumbs {
    align-self: flex-start;
}

.breadcrumbs {
    padding-left: 48px ;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 38px;
}

.breadcrumbs a {
    font-family: Gilroy;
    font-style: italic;
    font-weight: normal;
    font-size: 14px;
    line-height: 108.7%;

    color: #3F50FB;
}

@media (max-width: 1050px) {
    .cardContainer {
        grid-gap: 57px;
        padding: 0 78px 123px 78px;
    }
}

@media (max-width: 970px){
    .cardContainer {
        grid-template-columns: repeat(2, minmax(250px, 315px));
        padding: 0 78px 103px 78px;
    }
}

@media (max-width: 780px){
    .cardContainer {
        grid-template-columns: repeat(1, minmax(250px, 1fr));
        padding: 0 78px 63px 78px;
    }
}
