.card {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    justify-content: flex-end;
    min-height: 300px;
    overflow: hidden;
    border-radius: 10px;
    transition: transform .2s ease;
    cursor: pointer;
}

.card:hover {
    transform: scale(1.05);
}

.row {
    grid-column: span 2;
}

.column {
    grid-row: 4/6;
    grid-column: 3/4;
}

.column img {
    object-position: bottom;
    height: 100%;
}

.cardImage {
    position: absolute;
    overflow: hidden;
    width: 100%;
    height: 100%;
    border-radius: 10px;
}

.cardImage > img {
    left: 0;
    top: 0;
    object-fit: contain;
    position: absolute;
    min-width: 100%;
    object-position: center;
    z-index: 1;
    border-radius: 10px;
}

.cardContent {
    z-index: 2;
    margin: 22px 0 29px;
    text-align: start;
    font-family: Gilroy;
    font-style: normal;
    font-weight: normal;
    font-size: 21px;
    line-height: 24px;
    max-width: 524px;
    padding: 0 24px 0 24px;
    align-self: flex-start;
    color: #fff;
}

.cardContent p {
    font-family: Gilroy;
    font-style: normal;
    font-weight: normal;
    font-size: 21px;
    line-height: 24px;
    max-width: 524px;
}

.cardFooter {
    z-index: 2;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0 24px 31px 24px;
}

.cardFooter > :nth-child(2){
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
}

.date {
    font-family: Gilroy;
    font-style: normal;
    font-weight: 900;
    font-size: 14px;
    line-height: 18px;

    letter-spacing: 0.5px;
    text-transform: uppercase;

    color: #fff;
}

.viewsCount {
    margin-left: 11px;
    font-family: Gilroy;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;

    color: #fff;
}

@media (max-width: 970px) {
    .column {
        grid-row: 4/6;
        grid-column: 2/3;
    }
}

@media (max-width: 780px) {
    .cardFooter {
        margin: auto 0 0 0;
    }

    .card {
        display: flex;
        flex-direction: column;
        align-items: center;
        grid-column: span 1;
        grid-row:span 1;
        border-radius: 0;
    }

    .cardImage {
        position: relative;
        overflow: hidden;
        width: 100%;
        height: 163px;
        border-radius: 10px;
    }

    .cardImage > img {
        left: 0;
        top: 0;
        object-fit: cover;
        position: absolute;
        width: 100%;
        object-position: center;
    }

    .cardContent {
        margin: 22px 0 29px;
        color: #333333;
        padding: 0;
    }

    .cardFooter {
        width: 100%;
        margin: auto 0 0 0;
        padding: 0;
    }

    .date {
        color: #3C3C3C;
    }

    .viewsCount {
        margin-left: 11px;

        color: #3C3C3C;
    }



}


