.main {
    width: 100%;
    background: #2B2B2B;
    display: flex;
    align-self: center;
    justify-content: center;
}

.container {
    max-width: 1280px;
    padding: 46px 48px 50px 48px;
    width: 100%;
    display: flex;
    flex-direction: row;
}

.title {
    font-family: Gilroy;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 14px;

    display: flex;
    align-items: center;
    letter-spacing: 0.1em;
    text-transform: uppercase;

    color: #FFFFFF;
    margin-bottom: 30px;
}

.link {
    font-family: Gilroy;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 14px;

    display: flex;
    align-items: center;

    color: #F5F5F5;
}

.links {
    display: flex;
    flex-direction: row;
    width: 50%;
    text-align: start;
    color: #fff;
    justify-content: space-between;
}

.links li {
    margin-bottom: 17px;
}

.links a {
    font-family: Gilroy;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 14px;

    display: flex;
    align-items: center;

    color: #F5F5F5;
}

.links > :nth-child(2) {
    margin: 0 auto;
}

.formLinks {
    display: flex;
    flex-direction: row;
}

.formLinks a {
    margin-right: 52px;
    cursor: pointer;
    transition: transform .2s;
}

.formLinks a:hover {
    transform: scale(1.1);
}

.form {
    margin: 36px 0 35px;
    display: flex;
    flex-direction: column;
}

.label {
    font-family: Gilroy;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 14px;

    display: flex;
    align-items: center;
    letter-spacing: 0.1em;

    color: #FFFFFF;
    margin-bottom: 14px;
}

.footerInput {
    background: #F5F5F5;
    border-radius: 10px;
    font-family: Gilroy;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 14px;

    display: flex;
    align-items: center;
    letter-spacing: 0.1em;

    width: 270px;
    height: 42px;

    color: #2B2B2B;
    padding: 14px 20px;
    border: none;
}

.description {
    font-family: Gilroy;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 18px;

    display: flex;
    align-items: center;
    letter-spacing: 0.375px;

    color: #717171;
    max-width: 458px;
    text-align: start;
    margin-bottom: 21px;
}

.copyright {
    font-family: Gilroy;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 12px;

    display: flex;
    align-items: center;
    letter-spacing: 0.375px;

    color: #717171;
}

@media (max-width: 780px) {
    .container {
        max-width: 1280px;
        padding: 36px 48px 40px 48px;
        width: 100%;
        display: flex;
        flex-direction: column;
    }
    .links {
        width: 100%;
    }
    .copyright {
        text-align: center;
        align-self: center;
    }
    .formLinks {
        margin-top: 10px;
    }
}

