.card {
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: transform .2s ease;
    cursor: pointer;
}

.card:hover {
    transform: scale(1.05);
}

.cardImage {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 163px;
    border-radius: 10px;
}

.cardImage > img {
    left: 0;
    top: 0;
    object-fit: cover;
    position: absolute;
    width: 100%;
    border-radius: 10px;
    object-position: center;
}

.cardContent {
    margin: 22px 0 29px;
    text-align: start;
    font-family: Gilroy;
    font-style: normal;
    font-weight: normal;
    font-size: 21px;
    line-height: 24px;
    padding: 0 30px 0 0;
    color: #333333;
}

.cardContent p {
    font-family: Gilroy;
    font-style: normal;
    font-weight: normal;
    font-size: 21px;
    line-height: 24px;
    max-width: 524px;
}

.cardFooter {
    width: 100%;
    margin: auto 0 0 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.date {
    font-family: Gilroy;
    font-style: normal;
    font-weight: 900;
    font-size: 14px;
    line-height: 18px;

    letter-spacing: 0.5px;
    text-transform: uppercase;

    color: #3C3C3C;
}

.viewsCount {
    margin-left: 11px;
    font-family: Gilroy;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;

    color: #3C3C3C;
}


