.menu {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 78px;
    margin-bottom: 60px;
}

.menuTitle {
    font-family: Gilroy;
    font-style: normal;
    font-weight: 900;
    font-size: 32px;
    line-height: 108.7%;

    color: #2B2B2B;
}

.list {
    list-style: none;
    display: flex;
    flex-direction: row;
}

.nav {

}

.link {
    margin: 0 10px;
}

.link a {
    font-family: Gilroy;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;

    color: #717171;
}

.link:hover a {
    color: #606060;
}

.listButton {
    display: none;
    width: 36px;
    height: 36px;
    background: #3F50FB;
    border-radius: 10px;
    border: 0;
    margin: 0 20px 0 auto;
}

.menuButton {
    width: 36px;
    height: 36px;
    background: #3F50FB;
    border-radius: 10px;
    border: 0;
}



@media (max-width: 1050px) {
    .link {
        margin: 0 5px;
    }
}

@media (max-width: 970px){
    .nav {
        display: none;
    }

    .listButton {
        display: block;
    }
}